<template>
  <div>
    <!-- 表格 -->
    <el-card class="box-card mt20">
      <!-- <div slot="header" class="clearfix" style="line-height: 40px">
        <span>打印参数列表</span>
      </div> -->
      <div class="">
        <el-form
          v-if="tableData.length==0"
         :inline="true" class="demo-form-inline">
          <el-form-item>
            <el-button type="primary" size="medium" plain @click="addBtn"
              ><i class="el-icon-plus"></i> 新增</el-button
            >
          </el-form-item>
        </el-form>
        <ELTable
          :tableCol="tableCol"
          :tableData="tableData"
          :listQuery="listQuery"
          :ispagination="ispagination"
          @currentChange="currentChange"
          @sizeChange="sizeChange"
        >
        </ELTable>
      </div>
    </el-card>
    <!-- 新增打印参数弹出层 -->
    <el-dialog
      :title="dialogTitle"
      width="620px"
      :visible.sync="dialogFlag"
      :close-on-click-modal="false"
    >
      <el-form
        class=""
        style="padding: 0 60px 0 20px"
        ref="dialogFormRef"
        :model="dialogForm"
        :rules="dialogFormRules"
        label-width="130px"
      >
        <el-form-item label="包装数量" prop="Package_Qty">
          <el-input v-model.number="dialogForm.Package_Qty"></el-input>
        </el-form-item>

        <el-form-item label="版本号" prop="Rv">
          <el-input v-model="dialogForm.Rv"></el-input>
        </el-form-item>

        <el-form-item label="料号" prop="Itemcode_C_Shipping">
          <el-input v-model="dialogForm.Itemcode_C_Shipping"></el-input>
        </el-form-item>

        <el-form-item label="供应商编码" prop="Supplier">
          <el-input v-model="dialogForm.Supplier"></el-input>
        </el-form-item>

        <el-form-item label="发货总箱数" prop="No_Ship">
          <el-input v-model.number="dialogForm.No_Ship"></el-input>
        </el-form-item>

        <el-form-item label="料号前缀" prop="CS_type">
          <el-input v-model="dialogForm.CS_type"></el-input>
        </el-form-item>
        <el-form-item label="箱号二维码长度" prop="Shipping_SN_length">
          <el-input v-model.number="dialogForm.Shipping_SN_length"></el-input>
        </el-form-item>
        <el-form-item label="产品编码长度" prop="Product_length">
          <el-input v-model.number="dialogForm.Product_length"></el-input>
        </el-form-item>
      </el-form>

      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFlag = false">取 消</el-button>
        <el-button type="primary" @click="dialogSave">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import ELTable from "@/components/formTablePage/ElTable";
import { baseManagement } from "@/request/api";
import { mapState, mapActions } from "vuex";
import axios from "axios";

export default {
  name: "UserList",
  components: {
    ELTable,
  },
  //   watch: {
  //     CurrentUserInfo: function (val, oldVal) {
  //       //当val变更之后在执行接下来的操作
  //       console.log(" val val val val val",val);
  //       console.log(" oldVal oldVal oldVal oldVal oldVal",oldVal);
  //     },
  //   },
  data() {
    return {
      // 筛选
      listQuery: {
        page: 1,
        page_size: 10,
        total: 0,
      },
      listQueryBase: {
        page: 1,
        page_size: 10,
        total: 0,
      },
      ispagination: true, // 分页是否显示
      // 新增打印参数弹出层
      dialogTitle: "",
      dialogFlag: false,
      dialogForm: {
        Package_Qty: "",
        Rv: "",
        Itemcode_C_Shipping: "",
        Supplier: "",
        No_Ship: "",
        CS_type: "",
        Shipping_SN_length: "",
        Product_length: "",
      },
      dialogFormBase: {
        Package_Qty: "",
        Rv: "",
        Itemcode_C_Shipping: "",
        Supplier: "",
        No_Ship: "",
        CS_type: "",
        Shipping_SN_length: "",
        Product_length: "",
      },
      dialogFormRules: {
        Package_Qty: [
          { required: true, message: "请输入包装数量", trigger: "blur" },
          { type: "number", message: "必须为数值", trigger: "blur" },
        ],

        Rv: [{ required: true, message: "请输入版本", trigger: "blur" }],
        Itemcode_C_Shipping: [
          { required: true, message: "请输入料号", trigger: "blur" },
        ],
        Supplier: [
          { required: true, message: "请输入供应商编码", trigger: "blur" },
        ],

        No_Ship: [
          { required: true, message: "请输入发货总箱数", trigger: "blur" },
          { type: "number", message: "必须为数值", trigger: "blur" },
        ],

        CS_type: [
          { required: true, message: "请输入料号前缀", trigger: "blur" },
        ],

        Shipping_SN_length: [
          { required: true, message: "请输入箱号二维码长度", trigger: "blur" },
          { type: "number", message: "必须为数值", trigger: "blur" },
        ],
        Product_length: [
          { required: true, message: "请输入产品编码长度", trigger: "blur" },
          { type: "number", message: "必须为数值", trigger: "blur" },
        ],
      },
      // 表格列
      tableCol: [
        {
          label: "料号",
          field: "Itemcode_C_Shipping",
          align: "center",
        },
        {
          label: "供应商编码",
          field: "Supplier",
          align: "center",
          width: 200,
        },
        {
          label: "版本",
          field: "Rv",
          align: "center",
          width: 120,
        },
        {
          label: "发货总箱数",
          field: "No_Ship",
          align: "center",
          width: 120,
        },

        {
          label: "包装数量",
          field: "Package_Qty",
          align: "center",
          width: 120,
        },

        {
          label: "料号前缀",
          field: "CS_type",
          align: "center",
        },
        {
          label: "箱号二维码长度",
          field: "Shipping_SN_length",
          align: "center",
          width: 150,
        },
        {
          label: "产品编码长度",
          field: "Product_length",
          align: "center",
          width: 150,
        },
        {
          label: "操作",
          colType: "solt",
          align: "center",
          width: 150,
          option: [
            {
              label: "修改",
              event: this.colEditBtn,
            },
            {
              label: "删除",
              event: this.colDeleteBtn,
            },
          ],
        },
      ],
      tableData: [],
    };
  },

  computed: {
    ...mapState(["WorkCodeNameList", "CurrentUserInfo"]),
  },
  methods: {
    ...mapActions(["getLogisticWorkCodeName"]),
    // 查询
    searchBtn() {
      this.listQuery.page = 1;
      this.getLogisticOperationSystem(this.listQuery);
    },
    // 重置
    resetForm() {
      this.listQuery = JSON.parse(JSON.stringify(this.listQueryBase));
      this.getLogisticOperationSystem(this.listQuery);
    },
    // 当前页面改变时
    currentChange(val) {
      this.listQuery.page = val;
      this.getLogisticOperationSystem(this.listQuery);
    },
    // 每页条数发生改变时
    sizeChange(val) {
      this.listQuery.page = 1;
      this.listQuery.page_size = val;
      this.getLogisticOperationSystem(this.listQuery);
    },
    // 打印参数列表 查询
    getLogisticOperationSystem(params) {

      baseManagement.getLogisticOperationSystem(params).then((res) => {
        console.log("打印参数列表 查询", res);
        if (res.code == 0) {
          this.tableData = res.data.data;
          this.listQuery.total = res.data.total;
        } else {
          this.$message({
            message: res.message,
            type: "error",
          });
        }
      });
    },

    // 打印参数列表 新增
    postLogisticOperationSystem(params) {
      baseManagement.postLogisticOperationSystem(params).then((res) => {
        console.log("打印参数列表 新增", res);
        if (res.code == 0) {
          this.dialogFlag = false;
          this.getLogisticOperationSystem(this.listQuery);
          this.$message({
            message: res.message,
            type: "success",
          });
        } else {
          this.$message({
            message: res.message,
            type: "error",
          });
        }
      });
    },

    // 打印参数列表 新增
    addBtn() {
      this.dialogTitle = "新增打印参数";
      this.dialogFlag = true;
      this.$nextTick(() => {
        this.$refs.dialogFormRef.clearValidate();
      });
      this.dialogForm = JSON.parse(JSON.stringify(this.dialogFormBase));
    },

    // 打印参数列表 修改
    colEditBtn(rowData) {
      console.log(rowData);
      this.dialogTitle = "修改打印参数";
      this.dialogFlag = true;
      this.$nextTick(() => {
        this.$refs.dialogFormRef.clearValidate();
      });

      this.dialogForm = {
        ID: rowData.ID,
        Package_Qty: rowData.Package_Qty,
        Rv: rowData.Rv,
        Itemcode_C_Shipping: rowData.Itemcode_C_Shipping,
        Supplier: rowData.Supplier,
        No_Ship: rowData.No_Ship,
        CS_type: rowData.CS_type,
        Shipping_SN_length: rowData.Shipping_SN_length,
        Product_length: rowData.Product_length,
      };
    },

    // 打印参数列表 修改
    getLogisticPutOperationSystem(params) {
      baseManagement.getLogisticPutOperationSystem(params).then((res) => {
        console.log("打印参数列表 修改", res);
        if (res.code == 0) {
          this.dialogFlag = false;
          this.getLogisticOperationSystem(this.listQuery);
          this.$message({
            message: res.message,
            type: "success",
          });
        } else {
          this.$message({
            message: res.message,
            type: "error",
          });
        }
      });
    },

    // 弹出层确认按钮
    dialogSave() {
      this.$refs.dialogFormRef.validate((valid) => {
        if (!valid) {
          this.$message({
            message: "请修改正确的数据格式！",
            type: "warning",
          });
          return;
        } else {
          //验证通过调用保存接口
          if (this.dialogTitle == "新增打印参数") {
            console.log("新增打印参数", this.dialogForm);
            this.postLogisticOperationSystem(this.dialogForm);
          } else if (this.dialogTitle == "修改打印参数") {
            console.log("修改打印参数", this.dialogForm);
            this.getLogisticPutOperationSystem(this.dialogForm);
          }
        }
      });
    },

    // 打印参数列表 删除
    getLogisticDeleteOperationSystem(params) {
      baseManagement.getLogisticDeleteOperationSystem(params).then((res) => {
        console.log("打印参数列表 删除", res);
        if (res.code == 0) {
          this.getLogisticOperationSystem(this.listQuery);
          this.$message({
            message: res.message,
            type: "success",
          });
        } else {
          this.$message({
            message: res.message,
            type: "error",
          });
        }
      });
    },
    colDeleteBtn(rowData) {
      this.$confirm("确定执行删除操作吗？", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      }).then(() => {
        this.getLogisticDeleteOperationSystem({ ID: rowData.ID });
      });
    },
  },
  mounted() {
    // 打印参数列表 查询
    this.getLogisticOperationSystem(this.listQuery);
  },
};
</script>

